import ActivityInfoDrawer from '@/components/Activity/ActivityInfoDrawer';
import { getActivityApplication } from '@/api/activityApplication';
import Pagination from '@/components/Pagination';
import { paginationRequest } from '@/api/pagination';
import RemarkModal from "./components/modal";
import FilterForm from "./../components/FilterForm";
var columns = [{
  title: '活动名称',
  dataIndex: 'activity.name'
}, {
  title: '申请人',
  dataIndex: 'application_user.real_name',
  sorter: true
}, {
  title: '申请理由',
  key: 'reason',
  dataIndex: 'reason',
  scopedSlots: {
    customRender: 'reason'
  }
}, {
  title: '审批备注',
  dataIndex: 'remark',
  scopedSlots: {
    customRender: 'remark'
  }
}, {
  title: '申请状态',
  dataIndex: 'status',
  scopedSlots: {
    customRender: 'status'
  }
}, {
  title: '申请日期',
  dataIndex: 'created_at'
}, {
  title: '操作',
  scopedSlots: {
    customRender: 'operation'
  }
}];
export default {
  components: {
    Pagination: Pagination,
    RemarkModal: RemarkModal,
    FilterForm: FilterForm,
    ActivityInfoDrawer: ActivityInfoDrawer
  },
  filters: {
    noNull: function noNull(val) {
      if (val == null) {
        return '暂无此数据';
      } else {
        return val;
      }
    },
    statusToText: function statusToText(val) {
      switch (val) {
        case 0:
          return '申请中';

        case 1:
          return '审批通过';

        case 2:
          return '审批不通过';
      }
    }
  },
  data: function data() {
    return {
      data: [],
      columns: columns,
      loading: false,
      paginationData: {},
      btnLoading: false,
      modalVisible: false,
      isApprovel: false,
      applicationId: null,
      infoDrawerVisible: false,
      aid: undefined
    };
  },
  created: function created() {
    this.init();
  },
  methods: {
    init: function init() {
      this.fetchData();
    },
    fetchData: function fetchData(params) {
      var _this = this;

      this.loading = true;
      getActivityApplication(params).then(function (res) {
        _this.data = res.data.data;
        _this.paginationData = res.data;
        _this.loading = false;
      });
    },
    handlePageChange: function handlePageChange(url) {
      var _this2 = this;

      this.loading = true;
      paginationRequest(url).then(function (res) {
        _this2.data = res.data.data;
        _this2.loading = false;
      });
    },
    handleModalDone: function handleModalDone() {
      this.modalVisible = false;
      this.applicationId = null;
      this.init();
    },

    /** 查看某一申请的活动详情 */
    handleInfoClick: function handleInfoClick(row) {
      this.aid = row.activity_id;
      this.infoDrawerVisible = true;
    },

    /** 筛选的过滤 */
    handleFilterClick: function handleFilterClick(params) {
      this.fetchData(params);
    }
  }
};