import request from '@/utils/request';
export function addActivityApplication(data) {
  return request({
    url: '/api/activity/application',
    method: 'post',
    data: data
  });
}
export function getActivityApplication(params) {
  return request({
    url: '/api/admin/activity/application',
    method: 'get',
    params: params
  });
}
export function confirmActivityApplication(id, params) {
  return request({
    url: "/api/admin/activity/application/".concat(id, "/confirmation"),
    method: 'put',
    params: params
  });
}
export function rejectActivityApplication(id, params) {
  return request({
    url: "/api/admin/activity/application/".concat(id, "/rejection"),
    method: 'put',
    params: params
  });
}
export function applicationHistory() {
  return request({
    url: '/api/user/activity/application/history',
    method: 'get'
  });
}
export function getActivityApplicationInProgressCount() {
  return request({
    url: '/api/admin/activity/application/progress/count',
    method: 'get'
  });
}